@import "_normalize";
@import "_variables";
@import url("https://fonts.googleapis.com/css?family=Lato:400,700|Roboto+Slab:700&display=swap&subset=latin-ext");
$button-height: 27px;
$button-width: 35px;
$color-background: #F5F5F5;
$color-main: #FF5252;
$color-active: #FFF;
$color-link: #FFF;

@mixin menuItem {
  text-align: center;
  font-weight: bold;
  font-size: 14px;
  width: 80px;
  line-height: 14px;
  opacity: 0;
  @media (max-width: 1024px) {
    padding: 8px;
    width: 100%;
  }
}

@mixin sideMenuItem {
  text-decoration: none;
  font-weight: bold;
  font-size: 14px;
  width: 80px;
  height: 30px;
  line-height: 14px;
  margin-top: 10px;
  text-align: right;
  @media (max-width: 1024px) {
    padding: 8px;
    width: 100%;
  }
}

@keyframes changeArrowPosition {
  0% {
    transform: translateY(10px);
  }
  50% {
    transform: translateY(15px);
  }
  100% {
    transform: translateY(10px);
  }
}

@keyframes changeXposition {
  0% {
    transform: translateX(20px);
  }
  25% {
    transform: translateX(60px);
  }
  100% {
    transform: translateX(0px);
  }
}

@keyframes menuOpacity {
  0% {
    opacity: 0.1;
  }
  50% {
    opacity: 0.6;
  }
  75% {
    opacity: 0.8;
  }
  100% {
    opacity: 1;
  }
}

.apple-section {
  width: 70%;
  margin: 64px auto;
  max-width: 1600px;
  display: grid;
  grid-column-gap: 16px;
  grid-row-gap: 16px;
  grid-template-columns: 1fr 1fr;
  margin-bottom: 64px;
  opacity: 1;
  &__left {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  &__right {
    width: 1fr;
  }
  @media (max-width: 1024px) {
    width: 80%;
    grid-template-columns: 1fr;
    font-size: 18px;
    line-height: 20px;
    letter-spacing: 1px;
  }
}

i.material-icons {
  font-size: 200px;
}

html {
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  background: url("../assets/img/background.svg");
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
  color: #333;
  font-family: "Lato", sans-serif;
  transition: background-color 1s ease;
}

.footer {
  width: 70%;
  margin: 64px auto;
  max-width: 1600px;
  text-align: center;
  @media (max-width: 1024px) {
    margin: 0px auto;
    width: 80%;
  }
  &__paragraph {
    text-align: center;
    @media (max-width: 1024px) {
      padding-bottom: 8px;
      grid-template-columns: 1fr;
      font-size: 20px;
    }
  }
  &__section {
    padding-bottom: 64px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    @media (max-width: 1024px) {
      padding-bottom: 8px;
      grid-template-columns: 1fr;
      font-size: 20px;
    }
  }
  &__subtitle {
    text-align: center;
    font-weight: bold;
  }
  &__title {
    padding-bottom: 32px;
    @media (max-width: 1024px) {
      display: none;
    }
  }
}

.main {
  width: 100%;
  margin: 0 auto;
  max-width: 1600px;
}

.left-margin {
  height: 100%;
  display: flex;
  justify-content: flex-start;
  position: fixed;
  top: 0px; //to make it stick to the top of the screen
  @media (max-width: 1024px) {
    visibility: hidden;
  }
  &__content {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
  &__logo {
    opacity: 0;
    animation: menuOpacity 1s ease-in-out 1;
    animation-delay: 0.4s;
    animation-fill-mode: forwards; //save last animation state
  }
  &__paragraph-up {
    margin-left: 30px;
    margin-top: 50px;
    transform: rotate(270deg);
    color: #1c448e;
  }
  &__paragraph-down {
    margin-left: 60px;
    margin-bottom: 50px;
  }
  &__social-link {
    color: #1c448e;
    opacity: 0;
    animation: menuOpacity 1s ease-in-out 1;
    animation-delay: 0.2s;
    animation-fill-mode: forwards; //save last animation state
  }
}

.material-icons {
  font-size: 144px;
  text-align: center;
}

.navigation {
  @media (max-width: 1024px) {
    z-index: 99;
  }
  width: 100%;
  overflow: hidden;
  margin-top: 70px;
  z-index: 100;
  transition: all 1s;
  &__list {
    display: flex;
    flex-flow: row wrap;
    justify-content: center;
    list-style: none;
    padding: 0;
    margin: 0;
    @media (max-width: 1024px) {
      flex-direction: column;
      align-items: center;
      display: none;
    }
    &--visible {
      @media (max-width: 1024px) {
        display: flex;
      }
    }
  }
  &__contact-button {
    position: absolute;
    top: 50px;
    right: 16px;
    width: 150px;
    height: 60px;
    background-color: transparent;
    border: 1px solid #1c448e;
    background-image: linear-gradient(to top, #1c448e 50%, transparent 50%);
    background-clip: text, padding-box;
    background-size: 100% 200%;
    background-position: top;
    transition: background-position 0.2s ease-in-out;
    opacity: 0;
    z-index: 100;
    @media (max-width: 1024px) {
      display: none;
    }
    &:hover {
      background-position: bottom;
      cursor: pointer;
      color: white;
    }
    &:focus {
      outline: 0;
    }
    animation: menuOpacity 1s ease-in-out 1;
    animation-delay: 1.4s;
    animation-fill-mode: forwards; //save last animation state
  }
  &__item-1 {
    @include menuItem;
    animation: menuOpacity 1s ease-in-out 1;
    animation-delay: 0.6s;
    animation-fill-mode: forwards; //save last animation state
  }
  &__item-2 {
    @include menuItem;
    animation: menuOpacity 1s ease-in-out 1;
    animation-delay: 0.8s;
    animation-fill-mode: forwards; //save last animation state
  }
  &__item-3 {
    @include menuItem;
    animation: menuOpacity 1s ease-in-out 1;
    animation-delay: 1s;
    animation-fill-mode: forwards; //save last animation state
  }
  &__item-4 {
    @include menuItem;
    animation: menuOpacity 1s ease-in-out 1;
    animation-delay: 1.2s;
    animation-fill-mode: forwards; //save last animation state
  }
  &__link {
    text-decoration: none;
    color: black;
    &::after {
      margin-top: 10px;
      content: "";
      display: block;
      width: 0;
      height: 2px;
      background: #1c448e;
      transition: width 0.3s;
    }
    &:hover::after {
      width: 100%;
    }
  }
  &__switcher {
    @media (max-width: 1024px) {
      border: none;
      background: none;
      position: absolute;
      top: 4px;
      right: 8px;
      z-index: 100;
    }
    @media (min-width: 1025px) {
      display: none;
    }
  }
}

.right-margin {
  height: 100%;
  display: flex;
  position: fixed;
  justify-content: flex-end;
  top: 0;
  right: 0;
  @media (max-width: 1024px) {
    display: none;
  }
  &__content {
    opacity: 0;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    text-align: center;
    animation: menuOpacity 1s ease-in-out 1;
    animation-delay: 1.6s;
    animation-fill-mode: forwards; //save last animation state
  }
  &__logo {
    margin-top: 20px;
    margin-right: 21px;
    color: #1c448e;
  }
  &__paragraph-down {
    z-index: 100;
    animation: changeArrowPosition 1.5s ease-in-out infinite;
  }
  &__social {
    margin-right: 20px;
    margin-bottom: 35px;
  }
  &__social-link {
    text-decoration: none;
    color: #1c448e;
  }
}

.service-section {
  width: 70%;
  margin: 64px auto;
  max-width: 1600px;
  display: grid;
  grid-column-gap: 16px;
  grid-row-gap: 16px;
  grid-template-columns: 1fr 1fr;
  margin-bottom: 200px;
  opacity: 1;
  @media (max-width: 1024px) {
    width: 80%;
    grid-template-columns: 1fr;
    font-size: 18px;
    line-height: 20px;
    letter-spacing: 1px;
  }
  &__left {
    order: 1;
    width: 1fr;
    @media (max-width: 1024px) {
      order: 2;
    }
  }
  &__right {
    order: 2;
    display: flex;
    justify-content: center;
    align-items: center;
    @media (max-width: 1024px) {
      order: 1;
    }
  }
}

.side-navigation {
  position: fixed;
  top: 110px;
  right: 5px;
  z-index: 100;
  opacity: 0.5;
  @media (max-width: 1024px) {
    visibility: hidden;
  }
  &__item-1 {
    @include sideMenuItem;
    transition: all 0.6s;
  }
  &__item-2 {
    @include sideMenuItem;
    transition: all 0.8s;
  }
  &__item-3 {
    @include sideMenuItem;
    transition: all 1.2s;
  }
  &__item-4 {
    @include sideMenuItem;
    transition: all 1.6s;
  }
  &__link {
    color: black;
    padding: 10px 10px 10px 10px;
    font-family: sans-serif;
    text-transform: uppercase;
    position: relative;
    text-decoration: none;
    display: inline-block;
    border: 1px solid transparent;
    transition: all 0.4s cubic-bezier(0.5, 0.24, 0, 1);
    &::before {
      content: "";
      position: absolute;
      left: 0px;
      bottom: 0px;
      z-index: -1;
      width: 0%;
      height: 1px;
      background: black;
      box-shadow: inset 0px 0px 0px black;
      display: block;
      transition: all 0.4s cubic-bezier(0.5, 0.24, 0, 1);
    }
    &:hover::before {
      width: 100%;
    }
    &::after {
      content: "";
      position: absolute;
      right: 0px;
      top: 0px;
      z-index: -1;
      width: 0%;
      height: 1px;
      background: black;
      transition: all 0.4s cubic-bezier(0.5, 0.24, 0, 1);
    }
    &:hover::after {
      width: 100%;
    }
    &:hover {
      border-left: 1px solid black;
      border-right: 1px solid black;
    }
  }
  &__list {
    list-style: none;
  }
}

.site-header {
  z-index: 0;
}

.scrollUp {
  transform: translateY(-100px);
}

.snake {
  opacity: 0;
  display: flex;
  justify-content: center;
  margin-top: 100px;
  transform: scale(0.3);
  margin-bottom: 100px;
  animation: menuOpacity 1s ease-in-out 1;
  animation-delay: 1.8s;
  animation-fill-mode: forwards; //save last animation state
  &__svg {
    animation: changeXposition 0.7s ease-in-out 1;
    animation-delay: 2s;
    animation-fill-mode: forwards; //save last animation state
  }
}

.section-js {
  width: 100%;
  min-height: 100vh;
}

.top-section {
  text-align: center;
  margin-top: 70px;
  @media (max-width: 1024px) {
    padding: 32px;
    margin-top: 1px;
  }
  &__button {
    opacity: 0;
    margin-top: 32px;
    width: 170px;
    height: 60px;
    background-color: transparent;
    border: 1px solid #1c448e;
    background-image: linear-gradient(to top, #1c448e 50%, transparent 50%);
    background-clip: text, padding-box;
    background-size: 100% 200%;
    background-position: top;
    transition: background-position 0.2s ease-in-out;
    animation: menuOpacity 1s ease-in-out 1;
    animation-delay: 1.6s;
    animation-fill-mode: forwards; //save last animation state
    &:focus {
      outline: 0;
    }
    &:hover {
      background-position: bottom;
      cursor: pointer;
      color: white;
    }
  }
  &__paragraph {
    opacity: 0;
    line-height: 10px;
    font-size: 20px;
    animation: menuOpacity 1s ease-in-out 1;
    animation-delay: 1.4s;
    animation-fill-mode: forwards; //save last animation state
    @media (max-width: 1024px) {
      font-size: 20px;
      line-height: 30px;
    }
  }
  &__subtitle {
    opacity: 0;
    font-family: "Roboto Slab", serif;
    font-size: 40px;
    animation: menuOpacity 1s ease-in-out 1;
    animation-delay: 1.2s;
    animation-fill-mode: forwards; //save last animation state
  }
  &__title {
    opacity: 0;
    font-size: 20px;
    font-weight: normal;
    line-height: 10px;
    animation: menuOpacity 1s ease-in-out 1;
    animation-delay: 1.4s;
    animation-fill-mode: forwards; //save last animation state
  }
}

.transport-section {
